import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';
import uploadImage from '../../components/upload-image/upload-image.vue'
let arr = ['所有人可见','仅自己可见'];
export default {
	data() {
		return {
			yinsi: '所有人可见',
			text: '',
			imgList: []
		}
	},
	components:{
		uniNavBar,
		uploadImage
	},
	/* 监听页面返回 */
	onBackPress(){
		if(!this.text && this.imgList.length<1){
			return;
		}
		uni.showModal({
			title: '提示',
			content: '是否保存为草稿',
			cancelText: '不保存',
			confirmText: '保存',
			success: res => {
				if(res.confirm){
					
				}else{
					
				}
				uni.navigateBack({
					delta: 1
				})
			},
		});
	},
	methods: {
		back(){
			uni.navigateBack({
				delta: 1
			})
		},
		//发布事件
		submit(){
			console.log("发布")
		},
		//隐私
		changelook(){
			//调用官方接口
			uni.showActionSheet({
				itemList: arr,
				success:(res)=>{
						this.yinsi = arr[res.tapIndex]
				}
			});
		},
		upload(arr){
			this.imgList = arr
		}
	}
}
